import {request, requestImg} from '../../utils/req.js'
import * as echarts from 'echarts'
import mpvueEcharts from 'mpvue-echarts'
export default {
	data() {
		return {
			poorAllData: {}, // 统计的所有数据
			poorAll: true,
			poorPeople: true,
			poorReason: true,
			// 贫困原因列表
			reasonData: [
				{
					id: 'jh',
					color: '#ED292A',
					name: '因病'
				},
				{
					id: 'gty',
					color: '#6ACDB0',
					name: '因残'
				},
				{
					id: 'fdhb',
					color: '#72B667',
					name: '因灾'
				},
				{
					id: 'hiu',
					color: '#ED9829',
					name: '缺技术'
				},
				{
					id: 'kh',
					color: '#74ADCE',
					name: '因学'
				}
			],
			villageColor: [], // 贫困村颜色区分
			villageData: [], // 贫困村数据
			manNum: [], // 贫困人分类， 男
			womanNum: [], // 贫困人分类， 女
			poorReasonColor: [],  // 贫困原因颜色区分
			poorReasonData: [], // 贫困原因数据
		};
	},
	components: {
		mpvueEcharts
	},
	onReady() {
		let that = this
		request('GET', '/api/statistics').then(res => {
			that.poorAllData = res.body.data
			let newArr = that.poorAllData.village.map(item => {
				that.villageColor.push(item.color)
				that.villageData.push({value: item.num, name: item.name})
			})
			that.$refs.lineChart.init()
			let newArr1 = that.poorAllData.sex.map(item => {
				if (item.sex === '男,') {
					that.manNum.push(0, item.num)
				} else if (item.sex === '女') {
					that.womanNum.push(item.num, 0)
				}
			})
			that.$refs.lineChart1.init()
			let newArr2 = that.poorAllData.reason.map(item => {
				that.poorReasonColor.push(item.color)
				that.poorReasonData.push({value: item.value, name: item.name})
			})
			that.$refs.lineChart2.init()
		})
	},
	computed: {
		options () {
			let that = this
			let option = {
				tooltip: {
					trigger: 'item',
					formatter: "{a} <br/>{b}: {c} ({d}%)",
					position: [10, 10]
				},
				color: that.villageColor,
				series: [
					{
						name:'',
						type:'pie',
						radius: ['50%', '90%'],
						clockWise : false,  //是否顺时针排布
						avoidLabelOverlap: false, //是否启用防止标签重叠策略，默认开启，在标签拥挤重叠的情况下会挪动各个标签的位置，防止标签间的重叠。
						label: {
							normal: {
								formatter: '{d}%',
								rich: {
									a: {
										color: '#999',
										align: 'center'
									},
									b: {
										fontSize: 16,
										lineHeight: 33
									},
								},
								position: 'inner'
							},
							emphasis: {
								show: true,
								textStyle: {
									fontSize: '30',
									fontWeight: 'bold'
								}
							}
						},
						labelLine: {
							normal: {
								show: false
							}
						},
						data: that.villageData
					}
				]
			}
			return option
		},
		options1 () {
			let that = this
			let option = {
				xAxis: {
					show: false,
					type: 'value',
					boundaryGap: [0, 0.01]
				},
				yAxis: {
					show: false,
					type: 'category',
					data: ['xx', 'hh']
				},
				series: [
					{
						name: '2011年',
						type: 'bar',
						data: that.manNum,
						color: '#6ACDB0'
					},
					{
						name: '2012年',
						type: 'bar',
						data: that.womanNum,
						color: '#ED9829'
					}
				],
				barWidth: '20px',
				barCategoryGap: '20%'
			}
			return option
		},
		options2 () {
			let that = this
			let option = {
				series: [
					{
						name:'访问来源',
						type:'pie',
						selectedMode: 'single',
						radius: [0, '50%'],
		
						label: {
							normal: {
								position: 'inner'
							}
						},
						labelLine: {
							normal: {
								show: false
							}
						},
						color:['#ED9829', '#72B667', '#BC95B6', '', ''],
						data:[
							{value:50, name:'主观因素', selected:true},
							{value:24, name:'因灾'},
							{value:126, name:'客观因素'}
						]
					},
					{
						name:'',
						type:'pie',
						radius: ['70%', '80%'],
						label: {
							normal: {
								formatter: '{b}: {c} ({d}%)',
								backgroundColor: '#eee',
								rich: {
									a: {
										color: '#999',
										align: 'center'
									},
									b: {
										fontSize: 14,
										lineHeight: 33
									},
								}
							}
						},
						color: that.poorReasonColor,
						data: that.poorReasonData
					}
				]
			}
			return option
		}
	},
	
	methods: {
		// 点击隐藏字符
		clear () {
			this.poorAll = false
		},
		clearPeople () {
			this.poorPeople = false
		},
		clearReason () {
			this.poorReason = false
		},
		// 点击显示字符
		hidden () {
			this.poorAll = true
		},
		hiddenPeople () {
			this.poorPeople = true
		},
		hiddenReason () {
			this.poorReason = true
		},
		// 贫困户总人数
		lineInit(e) {
			let { width, height } = e;
			let canvas = this.$refs.lineChart.canvas;
			echarts.setCanvasCreator(() => canvas);
				let lineChart = echarts.init(canvas, null, {
				width: width,
				height: height
			});
			canvas.setChart(lineChart);
			lineChart.setOption(this.options);
			this.$refs.lineChart.setChart(lineChart);
		},
		// 
		lineInit1(e) {
			let { width, height } = e;
			let canvas = this.$refs.lineChart1.canvas;
			echarts.setCanvasCreator(() => canvas);
				let lineChart1 = echarts.init(canvas, null, {
				width: width,
				height: height
			});
			canvas.setChart(lineChart1);
			lineChart1.setOption(this.options1);
			this.$refs.lineChart1.setChart(lineChart1);
		},
		//
		lineInit2(e) {
			let { width, height } = e;
			let canvas = this.$refs.lineChart2.canvas;
			echarts.setCanvasCreator(() => canvas);
				let lineChart2 = echarts.init(canvas, null, {
				width: width,
				height: height
			});
			canvas.setChart(lineChart2);
			lineChart2.setOption(this.options2);
			this.$refs.lineChart1.setChart(lineChart2);
		},
	}
};
